import React from 'react'

// Import typefaces
import 'typeface-montserrat'
import 'typeface-merriweather'

import profilePic from './profile-pic.gif'
import { rhythm } from '../utils/typography'

class Bio extends React.Component {
  render() {
    return (
      <div
        style={{
          display: 'flex',
          marginBottom: rhythm(2.5),
        }}
      >
        <img
          src={profilePic}
          alt={'Joshua Sheridan'}
          style={{
            marginRight: rhythm(1 / 2),
            marginBottom: 0,
            width: rhythm(2),
            height: rhythm(2),
          }}
        />
        <p>
          A blog about <a href="https://creature-share.com">Creatures</a> an open source scooter share project <br />
          Started by {' '}
          <a href="https://joshuasheridan.com">
            Joshua Sheridan
          </a>
        </p>
      </div>
    )
  }
}

export default Bio
